import { image } from 'sol-common';
import Snap from '../snap';
import { get2dimArray, getPointInMatrixForm, getRandomArrayElement, getTransposeArray, getUniquArray, shuffle } from '../utils/util';
import D1drag from '../components/D1drag';
import D1Animate from '../utils/D1Animate';

export default function DM000002({ canvas, ...p }) {
    const g = canvas;
    const callback = p.callback;

    const items = p.items;
    const position = p.position;

    const testG = g.g();

    test({
        g: testG,
        items,
        position
    });
}

const test = ({ g, items, position }) => {
    const { center, rows, columns, size } = position;
    const itemG = g.g().addClass('D1-item-group');
    const itemLength = items.length;
    const totalNum = rows * columns;
    const remainNum = totalNum - itemLength;
    const addItem = getRandomArrayElement(items, remainNum);
    const point = getPointInMatrixForm({
        startPoint: { x: center.cx, y: center.cy },
        rows,
        columns,
        gap: { x: size.width, y: size.height },
        center: true
    });
    const sameNum = 3;

    const shapes = shuffle(items.concat(addItem)).map((el, i) => {
        const { type, imageIndex: url } = el;
        const { x, y } = point[i];
        const imageG = image({ canvas: itemG, img: { url, scale: 0.7 }, x, y, center: true });

        imageG
            .rect(x - size.width / 2, y - size.height / 2, size.width, size.height)
            .attr({ stroke: 'blue', opacity: '0' })
            .after(imageG.select('use'));

        imageG.data('data', { type, index: i });

        return D1drag({
            dragItem: imageG,
            startCallback: () => dragStartCallback(imageG),
            moveCallback: () => dragMoveCallback(imageG),
            endCallback: () => dragEndCallback(imageG)
        });
    });

    const dragStartCallback = self => {
        itemG.append(self);
    };

    const dragMoveCallback = self => {
        // const dropIndex = getUserDropElementIndex(self);
    };

    const dragEndCallback = async self => {
        shapes.forEach(el => el.attr({ pointerEvents: 'none' }));
        const userIndex = self.data('data').index;
        const dropIndex = getUserDropElementIndex(self);

        if (typeof dropIndex === 'number') {
            const dropElement = shapes[dropIndex];

            await swapItem(self, dropElement);
            const { answer } = answerCheck();

            if (answer) await answerAnimHandler();
            else await swapItem(dropElement, self);
        } else {
            const { x, y } = point[userIndex];
            await self.moveAnimate({ x: x - size.width / 2, y: y - size.height / 2 });
        }

        shapes.forEach(el => el.attr({ pointerEvents: 'auto' }));
    };

    const answerAnimHandler = async (bool = true) => {
        while (bool) {
            const { answer, removeIndex } = answerCheck();
            if (answer) {
                await removeItem(removeIndex);
                const emptyIndex = await downItem(removeIndex);
                await fillItem(emptyIndex);
            } else break;
        }
    };

    const removeItem = async index => {
        await Promise.all(index.map(el => D1Animate.blink({ interval: 0.5, duration: 500, element: shapes[el], opacity: 1 })));
        await Promise.all(index.map(el => D1Animate.blink({ interval: 0.5, duration: 500, element: shapes[el], opacity: 1 })));
        await Promise.all(index.map(el => D1Animate.blink({ interval: 1, duration: 250, element: shapes[el], opacity: 1 })));
    };

    const downItem = async index => {
        shapes.forEach((el, i) => index.includes(i) && el.remove());
        const columnCheckArr = getTransposeArray(get2dimArray(shapes, columns));
        const totalIndex = shapes.map((el, i) => i);
        const remainIndex = [];
        const animate = [];
        columnCheckArr.forEach(el => {
            el.forEach((el2, i) => {
                const { index } = el2.data('data');
                if (!el2.removed) {
                    const count = el.reduce((p, c, j) => {
                        if (i < j && c.removed) return (p += 1);
                        else return p;
                    }, 0);
                    if (count > 0) {
                        const moveIndex = index + columns * count;
                        const { x, y } = point[moveIndex];
                        el2.data('data').index = moveIndex;
                        animate.push(el2.moveAnimate.bind(null, { x: x - size.width / 2, y: y - size.height / 2 }, window.mina.bounce, true));
                        remainIndex.push(moveIndex);
                    } else {
                        remainIndex.push(index);
                    }
                }
            });
        });
        const emptyIndex = totalIndex.filter(el => !remainIndex.includes(el));

        await Promise.all(animate.map(el => el()));
        return emptyIndex;
    };

    const fillItem = async index => {
        const nextEmptyIndex = getNextArrayElement(index);
        const animate = [];
        shapes.forEach((el, i) => {
            if (el.removed) {
                const index = nextEmptyIndex.next().value;
                const { type, imageIndex: url } = shuffle(getRandomArrayElement(items, 5))[0];

                const { x, y } = point[index];
                const imageG = image({ canvas: itemG, img: { url, scale: 0.7 }, x, y: 0, center: true });

                imageG
                    .rect(x - size.width / 2, -size.height / 2, size.width, size.height)
                    .attr({ stroke: 'blue', opacity: '0' })
                    .after(imageG.select('use'));

                imageG.data('data', { type, index });

                shapes[i] = D1drag({
                    dragItem: imageG,
                    startCallback: () => dragStartCallback(imageG),
                    moveCallback: () => dragMoveCallback(imageG),
                    endCallback: () => dragEndCallback(imageG)
                });

                animate[i] = shapes[i].moveAnimate.bind(null, { x: x - size.width / 2, y: y - size.height / 2 }, window.mina.bounce, true);
            }
        });
        shapes.sort((a, b) => a.data('data').index - b.data('data').index);

        await Promise.all(animate.map(el => el()));

        function* getNextArrayElement(arr) {
            for (let index = 0; index < arr.length; index++) yield arr[index];
        }
    };

    const swapItem = async (first, second) => {
        const firstIndex = first.data('data').index;
        const secondIndex = second.data('data').index;
        const indexes = [secondIndex, firstIndex];
        const elements = [first, second];
        const animates = indexes.map((el, i) => {
            const { x, y } = point[el];
            return elements[i].moveAnimate.bind(null, { x: x - size.width / 2, y: y - size.height / 2 });
        });

        await Promise.all(animates.map(el => el()));

        first.data('data').index = secondIndex;
        second.data('data').index = firstIndex;
        shapes.sort((a, b) => a.data('data').index - b.data('data').index);
    };

    const answerCheck = () => {
        const rowRemoveIndex = rowCheck();
        const columRemoveIndex = columnCheck();
        const totalRemoveIndex = getUniquArray([...rowRemoveIndex, ...columRemoveIndex]);
        return { answer: Boolean(totalRemoveIndex.length > 0), removeIndex: totalRemoveIndex };
    };

    const rowCheck = () => {
        let removeTargetIndex = [];
        const rowCheckArr = get2dimArray(shapes, columns);

        const rowResult = rowCheckArr.map(el3 => {
            const removeIndex = [];
            el3.forEach((el, i) => {
                const currentType = el.data('data').type;
                removeIndex[i] = [];
                el3.some((el2, j) => {
                    if (i > j) return false;
                    if (currentType === el2.data('data').type) {
                        removeIndex[i].push(el2.data('data').index);
                        return false;
                    } else return true;
                });
            });
            return removeIndex;
        });

        rowResult.forEach(el =>
            el.forEach(el2 => {
                if (el2.length > sameNum - 1) removeTargetIndex = [...removeTargetIndex, ...el2];
            })
        );

        return getUniquArray(removeTargetIndex);
    };

    const columnCheck = () => {
        let removeTargetIndex = [];
        const columnCheckArr = getTransposeArray(get2dimArray(shapes, columns));
        const columnResult = columnCheckArr.map(el3 => {
            const removeIndex = [];
            el3.forEach((el, i) => {
                const currentType = el.data('data').type;
                removeIndex[i] = [];
                el3.some((el2, j) => {
                    if (i > j) return false;
                    if (currentType === el2.data('data').type) {
                        removeIndex[i].push(el2.data('data').index);
                        return false;
                    } else return true;
                });
            });
            return removeIndex;
        });

        columnResult.forEach(el =>
            el.forEach(el2 => {
                if (el2.length > sameNum - 1) removeTargetIndex = [...removeTargetIndex, ...el2];
            })
        );
        return getUniquArray(removeTargetIndex);
    };

    const getUserDropElementIndex = userDragItem => {
        const { cx, cy } = userDragItem.getBBox();
        const data = userDragItem.data('data');
        let targetIndex;
        shapes.some((el, i) => {
            const shapeBBox = el.getBBox();
            if (Snap.path.isPointInsideBBox(shapeBBox, cx, cy) && data.index !== i) {
                targetIndex = i;
                return true;
            }
            return false;
        });
        return targetIndex;
    };

    const start = async () => {
        const { answer } = answerCheck();
        answerAnimHandler(answer);
    };

    start();
};
